<template>
	<div class="main">
		<van-nav-bar title="" @click-left="goBack" fixed left-arrow fixed />
		<section class="page-main" ref="wrapper">
			<div class="box">
				<p></p>
				<p></p>
				<p>LOGOAPI</p>
				<p></p>
				<p></p>
				<p>1ICP/IP</p>
				<p>2</p>
				<p>3BUG</p>
				<p>4</p>
				<p>5</p>
				<p>6</p>
				<p></p>
				<p>www.szcciot.com</p>
				<p></p>
				<p></p>
				<p> </p>
				<p>15</p>
				<p></p>

			</div>
		</section>
	</div>
</template>

<script>
import { Dialog } from "vant";
import BScroll from "better-scroll";
import axios from "axios";

export default {
    name: "myBook",
    data() {
        return {
            // query: this.$route.query,
        };
    },
    async mounted() {
		this.setupBetterScroll();
	},

    methods: {
        goBack() {
            this.$router.back();
        },

        logout() {
            this.$store.commit("accountLogout");
            this.$router.push({ name: "loginPage" });
        },
        async setupBetterScroll() {
            if (!this.scroll) {
                this.scroll = new BScroll(this.$refs.wrapper, {
                    tap: true,
                    click: true
                });
            } else {
                this.scroll.refresh();
            }
        }
    }
};
</script>

<style lang="scss" scoped>
$dcyColor: #282549;
.box {
	padding: 15px;
	background: #fff;
    p {
        line-height: 1.8;
        font-size: 15px;
        color: #666;
		margin: 10px 0;
		text-align: justify;
    }
}
</style>



