const init = (params, callbacks, modules) => {
	var appKey = params.appKey;
	var token = params.token;
	var navi = params.navi || "";

	modules = modules || {};
	var RongIMLib = modules.RongIMLib || window.RongIMLib;
	var RongIMClient = RongIMLib.RongIMClient;
	var protobuf = modules.protobuf || null;

	var config = {};

	//navi '120.92.10.214:8888'
	if (navi !== "") {
		config.navi = navi;
	}

	//api, '172.20.210.38:81:8888'
	var api = params.api || "";
	if (api !== "") {
		config.api = api;
	}

	//support protobuf url + function
	if (protobuf != null) {
		config.protobuf = protobuf;
	};

	var dataProvider = null;
	var imClient = params.imClient;
	if (imClient) {
		dataProvider = new RongIMLib.VCDataProvider(imClient);
	}
	RongIMLib.RongIMClient.init(appKey, dataProvider, config);

	var instance = RongIMClient.getInstance();

	// 
	RongIMClient.setConnectionStatusListener({
		onChanged: function (status) {
			// console.log(status);
			switch (status) {
				case RongIMLib.ConnectionStatus["CONNECTED"]:
				case 0:
					console.log("")
					callbacks.getInstance && callbacks.getInstance(instance);
					break;

				case RongIMLib.ConnectionStatus["CONNECTING"]:
				case 1:
					console.log("")
					break;

				case RongIMLib.ConnectionStatus["DISCONNECTED"]:
				case 2:
					console.log("")
					break;

				case RongIMLib.ConnectionStatus["NETWORK_UNAVAILABLE"]:
				case 3:
					console.log("")
					break;

				case RongIMLib.ConnectionStatus["CONNECTION_CLOSED"]:
				case 4:
					console.log("")
					break;

				case RongIMLib.ConnectionStatus["KICKED_OFFLINE_BY_OTHER_CLIENT"]:
				case 6:
					console.log("")
					break;

				case RongIMLib.ConnectionStatus["DOMAIN_INCORRECT"]:
				case 12:
					console.log("")
					break;
			}
		}
	});

	/*
	http://www.rongcloud.cn/docs/web.html#3

	
		1
		2 conversationType + targetId
		3
		4http://rongcloud.cn/docs/api/js/index.html
	*/
	RongIMClient.setOnReceiveMessageListener({
		// 
		onReceived: function (message) {
			// 
			console.log(": " + message.targetId);
			console.log(message);
			callbacks.receiveNewMessage && callbacks.receiveNewMessage(message);
		}
	});

	//
	RongIMClient.connect(token, {
		onSuccess: function (userId) {
			callbacks.getCurrentUser && callbacks.getCurrentUser({
				userId: userId
			});
			console.log("id" + userId);
		},
		onTokenIncorrect: function () {
			console.log('token');
		},
		onError: function (errorCode) {
			console.log(errorCode);
		}
	}, params.userId);
}

export default init;



