<template>
	<div class="main">
		<van-nav-bar title="" @click-left="goBack" fixed left-arrow fixed />
		<section class="page-main" ref="wrapper">
			<div class="box">
				<p>
				</p>
				<p></p>
				<p></p>
				<img src="@/assets/desc/6.jpg">
				<p></p>
				<p>GPS</p>
				<img src="@/assets/desc/4.jpg">
				<img src="@/assets/desc/3.jpg">
				<p></p>
				<p>7</p>
				<img src="@/assets/desc/8.jpg">
				<img src="@/assets/desc/9.jpg">
				<img src="@/assets/desc/7.jpg">
				<p></p>
				<p></p>
				<img src="@/assets/desc/5.jpg">
				<p></p>
				<p></p>
				<img src="@/assets/desc/1.jpg">
				<img src="@/assets/desc/2.jpg">

			</div>
		</section>
	</div>
</template>

<script>
import { Dialog } from "vant";
import BScroll from "better-scroll";
import axios from "axios";

export default {
    name: "myDesc",
    data() {
        return {
            // query: this.$route.query,
        };
    },
    async mounted() {
        // this.setupBetterScroll();
        this.$nextTick(() => {
            // document.title = "";
            this.setupBetterScroll();
        });
    },

    methods: {
        goBack() {
            this.$router.back();
        },

        logout() {
            this.$store.commit("accountLogout");
            this.$router.push({ name: "loginPage" });
        },
        async setupBetterScroll() {
            if (!this.scroll) {
                this.scroll = new BScroll(this.$refs.wrapper, {
                    tap: true,
                    click: true
                });
            } else {
                this.scroll.refresh();
            }
            setTimeout(() => {
                this.scroll.refresh();
            }, 1000);
        }
    }
};
</script>

<style lang="scss" scoped>
$dcyColor: #282549;
.box {
    padding: 15px;
    background: #fff;
    p {
        line-height: 1.8;
        font-size: 15px;
        color: #666;
        margin: 10px 0;
        text-align: justify;
    }
    img {
        max-width: 100%;
        margin: 10px 0;
        display: block;
    }
}
</style>



